#seguro_body {
  padding: 20px;
  height: 20vh;
}

#seguro_body2 {
  display: flex;
  justify-content: center;
}

.card_seguro {
  background-color: #495057;
  border: 1px solid white;
  padding: 5px;
  text-align: center;
  width: 500px;
  margin-top: 50px;
  margin-left: 10px;
  border-radius: 5px;
  box-shadow: 4px 4px 10px #495057;
}

.card_seguro2 {
  cursor: pointer;
  background-color: #495057;
  border: 1px solid white;
  padding: 5px;
  text-align: center;
  width: 500px;
  margin-top: 80px;
  border-radius: 5px;
  box-shadow: 4px 4px 10px #495057;
  transition: 1s;
}

.card_seguro2:hover {
  background-color: #383a3d;
}

.card_information {
  display: flex;
  border: 4px solid #495057;
  height: 50vh;
}

.title_seguro {
  color: white;
  font-size: 30px;
}

.contexto {
  color: white;
  font-size: 25px;
}

.preco_veiculo {
  color: white;
  font-size: 25px;
}

.color_assegurado {
  font-size: 20px;
  color: #52b788;
}

.img {
  width: 350px;
}
