#container_capital {
  margin-top: 40px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.capital_header {
  display: flex;
  align-items: center;
  width: 90%;
  padding: 50px;
  border-radius: 5px;
  box-shadow: 3px 3px 10px #e9ecef;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-around;
}

#capital_header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#cards_header {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.card_capital {
  width: 300px;
  height: 80px;
  margin: 10px;
  padding: 10px;
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  background-color: white;
  color: #9e9e9e;
  border-radius: 7px;
  box-shadow: 3px 3px 60px #cccdce;
}

.body_capital {
  display: flex;
  position: relative;
  flex-wrap: wrap;
  flex-direction: column;
  padding: 20px;
  justify-content: space-between;
  align-items: center;
}

.card {
  margin-top: 10px;
  border-radius: 10px;
  padding: 10px;
  justify-content: center;
}

.card_white {
  font-size: 20px;
  margin: 10px;
  border-radius: 10px;
  padding: 10px;
  box-shadow: 2px 2px 10px #cccdce;
  justify-content: center;
}

.central {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.search {
  font-size: 25px;
  height: 30px;
  border: none;
  background-color: transparent;
  border-bottom: 1px solid #cccdce;
  text-align: center;
}

.search:focus {
  box-shadow: 0 0 0 0;
  outline: 0;
}

.text_capital {
  font-size: 50px;
}

.space_button {
  margin: 5px;
}

.extratos {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  margin-bottom: 10px;
}

.ext_conteudo {
  display: none;
}

.btn_close {
  cursor: pointer;
}

.card_codigo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  box-shadow: 3px 3px 10px #cccdce;
  width: 500px;
  background-color: #fafafa;
  border-radius: 10px;
}

.convite {
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
}

.card_convite {
  display: flex;
  flex-direction: column;
  width: 500px;
  margin: 4px;
  box-shadow: 2px 2px 10px #9e9e9e;
  border-radius: 5px;
  text-align: center;
  align-items: center;
  background-color: #f7f7f7;
}

.text_title_convite {
  font-size: 35px;
  color: #78c6a3;
}
