* {
  margin: 0;
}

.body {
  margin-top: 100px;
  height: 100vh;
  background-image: url("https://cdn.dribbble.com/users/4908/screenshots/2787171/invoice-animation-dribbble.gif");
  background-repeat: no-repeat;
  background-size: 900px;
  filter: grayscale(70%);
}

.title {
  text-align: center;
  font-size: 60px;
  color: white;
}

.header_top {
  background-color: white;
  height: 10vh;
}

.card-login {
  display: flex;
  background-color: white;
  width: 400px;
  text-align: center;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  box-shadow: 4px 4px 20px #6290b9;
  padding: 20px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 40px;
}

.header_top {
  text-align: center;
  padding-top: 50px;
  font-size: 240%;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-style: oblique;
}

.conteudo-column {
  flex-direction: column;
}

@media screen and (min-width: 300px) and (max-width: 400px) {
  .card-login {
    width: 350px;
  }
}

@media screen and (min-width: 401px) and (max-width: 600px) {
  .card-login {
    width: 420px;
  }
}
