/* Title: font-family: 'Bungee', cursive; */
@import url("https://fonts.googleapis.com/css2?family=Bungee&display=swap");
/* ------------------------------------------------------------------------ */

/* Text Corpo: font-family: 'Darker Grotesque', sans-serif;  */
@import url("https://fonts.googleapis.com/css2?family=Darker+Grotesque&display=swap");

* {
  font-family: "Darker Grotesque", sans-serif;
}

.navbar {
  width: 300px;
  float: left;
  height: 100vh;
  text-align: center;
  background-color: #ffffff;
  box-shadow: 4px 4px 20px #f1f1f0;
}

.background {
  padding-top: 20px;
  padding-left: 400px;
  background-image: url("https://prospergate.com/wp-content/uploads/2021/10/finance.gif");
  height: 100vh;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.conteudo_nav {
  height: 100px;
  color: rgb(104, 104, 104);
}

.title_nav {
  margin-top: 10px;
  font-size: 40px;
  font-family: "Bungee", cursive;
}

.icons {
  margin-top: 10px;
  font-size: 30px;
}

.icon {
  margin-right: 10px;
  font-size: 20px;
}

.text-nav {
  font-size: 25px;
}

.card_option {
  cursor: pointer;
  transition: 0.2s;
}

.card_option_active {
  cursor: pointer;
  transition: 0.2s;
  background-color: #d8d8d3;
}

.img2 {
  width: 100%;
  height: 200px;
  border-radius: 10px 10px 0px 0px;
}

.card_option:hover {
  background-color: #f5f5f4;
}

.button_primary {
  color: white !important;
  background-color: #001d3d !important;
}

.button_primary:hover {
  background-color: #023061 !important;
}

.conteudo2 {
  float: left;
  width: 19%;
  margin-top: 10%;
  margin-left: 20px;
  height: 440px;
  border-radius: 10px;
  text-align: center;
  background-color: #fbfbfb;
  box-shadow: 4px 4px 20px #cfcfce;
}

p {
  margin: 20px;
  font-size: 20px;
}

@media only screen and (max-width: 1900px) {
  .conteudo2 {
    width: 300px;
    height: 440px;
  }
  .background {
    height: 150vh;
  }

  .img {
    width: 300px;
    height: 180px;
  }
}
