.navbar_index {
  background-color: #f8f9fa;
  color: #6c757d;
  font-size: 30px;
  display: flex;
  justify-content: center;
  height: 10vh;
  border-bottom: 1px solid #dee2e6;
}

.f-left {
  float: left;
}

.f-rigth {
  float: right;
}

.p_sair {
  cursor: pointer;
  margin-top: 20px;
  margin-left: 50px;
}

.redme {
  cursor: pointer;
  margin-top: 20px;
  margin-left: 50px;
}

.ft_sair {
  margin-left: 5px;
}

.c-center {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
}

.card_stiticks {
  margin: 20px;
  width: 200px;
  border-radius: 10px;
  box-shadow: 4px 4px 20px #5a5a5a;
  color: white;
  padding: 20px;
  text-align: center;
  font-size: 30px;
  background-color: #495057;
}

.card_stiticks_pendente {
  margin: 20px;
  width: 200px;
  border-radius: 10px;
  box-shadow: 4px 4px 20px #800f2f;
  color: white;
  padding: 20px;
  text-align: center;
  font-size: 30px;
  background-color: #800f2f;
}

.campo {
  display: flex;
  flex-direction: row;
  padding: 20px;
}

.cont1 {
  margin-top: 10vh;
  display: flex;
  flex-direction: column;
  background-color: transparent;
  border-right: 1px solid #ced4da;
  padding: 30px;
  width: 550px;
}
.cont2 {
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  padding-left: 10px;
  padding-top: 20px;
}

.card_extrato {
  display: flex;
  flex-direction: column;
  margin: 20px;
  box-shadow: 1px 1px 4px #495057;
  background-color: #ffffff;
  border-radius: 5px;
  padding: 20px;
  height: 200px;
}

.redme {
  display: none;
}

.cardsPropagandas {
  display: flex;
  flex-direction: column;
  text-align: center;
  background-color: #ffffff;
  box-shadow: 1px 1px 4px #495057;
  border-radius: 5px;
  margin: 20px;
  width: 500px;
}

.img_card {
  height: 200px;
  width: 500px;
}

#component_perfil {
  justify-content: end;
}

.card_jogo_1 {
  display: flex;
  margin: 20px;
  flex-direction: column;
  flex-wrap: wrap;
  padding: 10px;
  border-radius: 15px;
  background-color: #7d8288;
  box-shadow: 4px 4px 10px #49574f;
  width: 400px;
}

.card_header {
  text-align: center;
  font-size: 25px;
  text-shadow: 4px 4px 8px #495057;
  color: white;
}

.font-white {
  margin-left: 20px;
  color: white;
  font-size: 25px;
}

.font-orange {
  margin-left: 20px;
  color: #faa307;
  font-size: 25px;
}

.font-cinza {
  margin: 5px;
  padding: 5px;
  color: #6c757d;
  font-size: 25px;
}

.border_r {
  border-right: 1px solid #a1a4a8;
}
