.button {
  padding: 10px;
  width: 200px;
  border: none;
  cursor: pointer;
  margin: 20px;
  box-shadow: 4px 4px 10px rgb(194, 194, 194);
  border-radius: 5px;
  font-weight: 400;
}

.button1 {
  padding: 10px;
  width: 200px;
  border: none;
  cursor: pointer;
  margin: 20px;
  border-radius: 5px;
  font-weight: 400;
}

.button_op {
  padding: 10px;
  width: 200px;
  border: none;
  cursor: pointer;
  margin: 14px;
  height: 50px;
  box-shadow: 4px 4px 10px rgb(194, 194, 194);
  border-radius: 5px;
  font-weight: 700;
}

.btn_voltar {
  cursor: pointer;
}

.color_success {
  color: white;
  font-size: 20px;
  background-color: #52b788;
  transition: 0.2s;
}

.color_success:hover {
  background-color: #3e926b;
}

.color_blue {
  color: white;
  font-size: 20px;
  background-color: #003566;
  transition: 0.2s;
}

.color_blue:hover {
  background-color: #1f57b1;
}

.color_cinza {
  color: white;
  font-size: 20px;
  background-color: #a3a3a3;
  border: 2px solid #cecece;
  transition: 0.2s;
}

.color_cinza:hover {
  color: white;
  font-size: 20px;
  background-color: #727272;
  transition: 0.2s;
}

.color_red {
  cursor: pointer;
  color: white;
  background-color: #700606;
  padding: 10px;
  border: 1px solid white;
  border-radius: 10px;
  font-size: 20px;
  transition: 0.2s;
}

.color_red:hover {
  background-color: #530505;
}

.input {
  margin: 10px;
}

.link {
  margin: 10px;
  font-size: 20px;
  color: #003566;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  text-decoration: none;
  transition: 0.2s;
}

.link:hover {
  color: #5a73e2;
}

.row {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.btn_acoes {
  display: flex;
  flex-direction: column;
  cursor: pointer;
  color: white;
  text-align: center;
  font-size: 40px;
  margin-bottom: 10px;
  width: 200px;
  border-radius: 10px;
  margin-left: 20px;
  margin-top: 20px;
  background-color: #727272;
  box-shadow: 2px 2px 10px #afafaf;
  height: 120px;
  transition: 0.2s;
}

.btn_acoes:hover {
  background-color: #fbfbfb;
  color: #727272;
}

.btn_acoes_disabled {
  display: flex;
  flex-direction: column;
  cursor: pointer;
  color: #727272;
  text-align: center;
  font-size: 40px;
  margin-bottom: 10px;
  width: 200px;
  border-radius: 10px;
  margin-left: 20px;
  margin-top: 20px;
  background-color: #700606;
  box-shadow: 2px 2px 10px #afafaf;
  height: 120px;
  transition: 0.2s;
}

.btn_acoes_disabled:hover {
  background-color: #530505;
  color: #727272;
}

.position_center {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

#campo_principal {
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

#campo_principal.hide {
  opacity: 0;
  pointer-events: none;
}

#capital {
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

#capital.capital_hidecap {
  opacity: 0;
  pointer-events: none;
}

#campo_admin {
  opacity: 1;
  transition: opacity 0.5s linear;
}

#campo_debVehs {
  opacity: 1;
  transition: opacity 0.5s linear;
}

#campo_seguros {
  opacity: 1;
  transition: opacity 0.5s linear;
}

#campo_seguros.seguroshiden {
  opacity: 0;
  pointer-events: none;
}

#admin_seguros {
  opacity: 1;
  transition: opacity 0.5s linear;
}

#admin_seguros.adm_seguroshiden {
  opacity: 0;
  pointer-events: none;
}

#campo_debVehs.seguroshiden {
  opacity: 0;
  pointer-events: none;
}

.none {
  display: none;
}

.block {
  display: block;
}

#campo_admin.admhiden {
  opacity: 0;
  pointer-events: none;
}

#campo_debVehs.debvehshiden {
  opacity: 0;
  pointer-events: none;
}

input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
input[type="number"] {
  -moz-appearance: textfield;
  appearance: textfield;
}

.table_one,
th,
td {
  width: 800px;
  padding: 10px;
  margin-top: 10px;
  font-size: 20px;
  text-align: center;
  border: 1px solid #a3a3a3;
  border-collapse: collapse;
}

.table_one thead {
  font-weight: 700;
  background-color: #95d5b2;
}
